	module.exports = {"data":{"markdownRemark":{"html":"<h2>Alignment</h2>\n<p>Alignment is a fundamental typographic practice wherein we set the flow of text relative to an entire page, column, table cell, or tab in the document. Alignment is sometimes referred to as text alignment, text justification, or type justification.</p>\n<p>There are mainly four types of text alignment <strong>Flush Left</strong>, <strong>Centered</strong>, <strong>Flush Right</strong> and <strong>Justified</strong>.</p>\n<blockquote>\n<p>NOTE: The edge of a page or column is known as a margin, and the gap between columns is known as a gutter.</p>\n</blockquote>\n<h4>Flush Left</h4>\n<p>In the <em>flush left</em> alignment the text is aligned along the left margin or gutter. The Article you are reading now is an example of a left aligned text block.</p>\n<h4>Centered</h4>\n<p>In a <em>centered</em> alignment the text is aligned along the center of the page or column. The gap beteween the left and right margin from the text will be the same in this type of alignment.</p>\n<h4>Flush Right</h4>\n<p>In the <em>flush right</em> alignment the text is aligned along the right margin or gutter.</p>\n<h4>Justified</h4>\n<p>In a <em>justified</em> text, the spacing between words and letters are modified such that the text block falls flush with both margins. Justification is a common practice in print media. During justification it is customary to treat the last line separately by aligning it to either left or right depending on the language.</p>\n<p>Modern typesetting programs sometimes offer four additional justification optons namely <em>left justify</em>, <em>right justify</em>, <em>center justify</em> and <em>full justify</em>. These variants specify whether the full lines of a paragraph are aligned to the left, right, centered, or are fully justified.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Wikipedia Article <a href=\"https://en.wikipedia.org/wiki/Typographic_alignment\">Link</a></li>\n<li>Web Style guide <a href=\"http://webstyleguide.com/wsg2/type/align.html\">Link</a></li>\n</ul>","fields":{"slug":"/typography/alignment/"},"frontmatter":{"title":"Alignment"}}},"pathContext":{"slug":"/typography/alignment/"}}

